// export default Footer;

import React from 'react';
import {
  Mail,
  Phone,
  MapPin,
  Facebook,
  Twitter,
  Instagram,
  Linkedin,
  Youtube,
  ArrowRight,
  Star,
  Globe
} from 'lucide-react';
import { useNavigate } from 'react-router-dom';
import './Footer.css';

const Footer = () => {
  const navigate = useNavigate();
  const currentYear = new Date().getFullYear();

  const companyInfo = {
    name: 'Pixel Horse',
    address: '3-5-1094/14/1, 4th Floor, Kammalamma Nilayam, Sri Venkateshwara Colony, Street no. 1, Narayanaguda, Hyderabad, 500029',
    email: 'admin@pixelhorse.in',
    phone: '+91 99495 62299',
    website: 'www.pixelhorse.co'
  };

  const footerLinks = {
    services: [
      { name: '3D Anamorphic Advertising', href: '/3d-advertising' },
      // { name: 'IT Services & Solutions', href: '/it-solutions' },
      { name: 'Digital Marketing', href: '/digital-marketing' },
      { name: 'AR/VR Development', href: '/arvr-development' },
      { name: 'Ui/UX', href: '/uiux-design' },
      { name: 'Animation Creation', href: '/animation-creation' }
    ],
    company: [
      { name: 'About Us', href: '/about-us' },
      { name: 'Our Team', href: '/our-team' },
      { name: 'Careers', href: '/careers' },
      { name: 'Press & Media', href: '/press-media' },
      { name: 'Awards', href: '/awards' }
    ],
    resources: [
      { name: 'Blog & Insights', href: '#' },
      { name: 'Case Studies', href: '#' },
      { name: 'White Papers', href: '#' },
      { name: 'Documentation', href: '#' },
      { name: 'API Reference', href: '#' }
    ],
    support: [
      { name: 'Help Center', href: '/help-center' },
      { name: 'Contact Support', href: '/contact-support' },
      { name: 'System Status', href: '/system-status' },
      { name: 'Privacy Policy', href: '/privacy-policy' },
      { name: 'Terms of Service', href: '/terms-of-service' }
    ]
  };

 const socialLinks = [
    { icon: <Facebook />, color: '#1877F2' },
    { icon: <Twitter />, color: '#1DA1F2' },
    { icon: <Instagram />, color: '#E4405F' },
    { icon: <Linkedin />, color: '#0A66C2' },
    { icon: <Youtube />, color: '#FF0000' }
  ];
  const achievements = [
    { number: '50+', label: 'Projects' },
    { number: '10+', label: 'Clients' },
    { number: '20+', label: 'Awards' },
    { number: '1+', label: 'Years' }
  ];

  const handleNavigate = (path) => {
    navigate(path);
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };


  return (
    <footer className="ph-footer">
      <div className="ph-footer-background">
        <div className="ph-footer-orb ph-orb-1"></div>
        <div className="ph-footer-orb ph-orb-2"></div>
        <div className="ph-footer-orb ph-orb-3"></div>
      </div>

      <div className="ph-footer-container">
        <div className="ph-footer-main">
          <div className="ph-footer-brand">
            <div className="ph-brand-logo">
              <div className="ph-logo-container">
                <div className="ph-logo-icon-container">
                  {/* <img src="/src/public/images/Logo.png" alt="Pixel Horse Logo" className="ph-logo-image" /> */}
                  <img src="public/images/Logo.png" alt="Pixel Horse Logo" className="ph-logo-image" />
                </div>
                <div className="ph-logo-glow"></div>
              </div>
              {/* <div className="ph-brand-text">
                <h3 className="ph-brand-name">Pixel Horse</h3>
                <p className="ph-brand-tagline">3D Innovation Hub</p>
              </div> */}
            </div>
            
            <p className="ph-brand-description">
              Transforming digital experiences through cutting-edge 3D technology, 
              AI systems, and innovative solutions that push the boundaries of imagination.
            </p>

            <div className="ph-achievements-stats">
              {achievements.map((achievement, index) => (
                <div key={index} className="ph-achievement-stat">
                  <div className="ph-stat-number">{achievement.number}</div>
                  <div className="ph-stat-label">{achievement.label}</div>
                </div>
              ))}
            </div>

            <div className="ph-brand-contact">
              <div className="ph-contact-item">
                <Mail className="ph-contact-icon" />
                <span>{companyInfo.email}</span>
              </div>
              <div className="ph-contact-item">
                <Phone className="ph-contact-icon" />
                <span>{companyInfo.phone}</span>
              </div>
              <div className="ph-contact-item">
                <MapPin className="ph-contact-icon" />
                <span>{companyInfo.address}</span>
              </div>
            </div>
          </div>

          <div className="ph-footer-links">
            <div className="ph-links-section">
              <h4 className="ph-links-title">
                <Star className="ph-title-icon" />
                Our Services
              </h4>
              <ul className="ph-links-list">
                {footerLinks.services.map((link, index) => (
                  <li key={index} className="ph-link-item">
                    <a href={link.href} className="ph-link" onClick={(e) => { e.preventDefault(); setCurrentSection(link.href.slice(1)); }}>
                      <ArrowRight className="ph-link-arrow" />
                      {link.name}
                    </a>
                  </li>
                ))}
              </ul>
            </div>

            <div className="ph-links-section">
              <h4 className="ph-links-title">
                <Globe className="ph-title-icon" />
                Company
              </h4>
              <ul className="ph-links-list">
                {footerLinks.company.map((link, index) => (
                  <li key={index} className="ph-link-item">
                    <a href={link.href} className="ph-link" onClick={(e) => { e.preventDefault(); setCurrentSection(link.href.slice(1)); }}>
                      <ArrowRight className="ph-link-arrow" />
                      {link.name}
                    </a>
                  </li>
                ))}
              </ul>
            </div>

            <div className="ph-links-section">
              <h4 className="ph-links-title">
                <Star className="ph-title-icon" />
                Resources
              </h4>
              <ul className="ph-links-list">
                {footerLinks.resources.map((link, index) => (
                  <li key={index} className="ph-link-item">
                    <a href={link.href} className="ph-link" onClick={(e) => { e.preventDefault(); setCurrentSection(link.href.slice(1)); }}>
                      <ArrowRight className="ph-link-arrow" />
                      {link.name}
                    </a>
                  </li>
                ))}
              </ul>
            </div>

            <div className="ph-links-section">
              <h4 className="ph-links-title">
                <Globe className="ph-title-icon" />
                Support
              </h4>
              <ul className="ph-links-list">
                {footerLinks.support.map((link, index) => (
                  <li key={index} className="ph-link-item">
                    <a href={link.href} className="ph-link" onClick={(e) => { e.preventDefault(); setCurrentSection(link.href.slice(1)); }}>
                      <ArrowRight className="ph-link-arrow" />
                      {link.name}
                    </a>
                  </li>
                ))}
              </ul>
            </div>
          </div>
        </div>

        <div className="ph-footer-newsletter">
          <div className="ph-newsletter-content">
            <div className="ph-newsletter-text">
              <h4 className="ph-newsletter-title">Stay Updated</h4>
              <p className="ph-newsletter-description">
                Subscribe to our newsletter for the latest updates on 3D innovations and industry insights.
              </p>
            </div>
            <div className="ph-newsletter-form">
              <div className="ph-form-group">
                <input 
                  type="email" 
                  placeholder="Enter your email address"
                  className="ph-newsletter-input"
                />
                <button className="ph-newsletter-button">
                  <span>Subscribe</span>
                  <ArrowRight className="ph-button-arrow" />
                </button>
              </div>
            </div>
          </div>
        </div>

        {/* BOTTOM */}
        <div className="ph-footer-bottom">
          <div className="ph-social-links">
            {socialLinks.map((social, index) => (
              <span
                key={index}
                className="ph-social-link"
                style={{ '--social-color': social.color }}
              >
                {social.icon}
              </span>
            ))}
          </div>

          <div className="ph-footer-legal">
  <p> {currentYear} Pixel Horse. All rights reserved.</p>

  <div className="ph-legal-links">
    <button onClick={() => handleNavigate('/privacy-policy')}>
      Privacy Policy
    </button>
    <span></span>
    <button onClick={() => handleNavigate('/terms-of-service')}>
      Terms of Service
    </button>
    <span></span>
    <button onClick={() => handleNavigate('/cookie-policy')}>
      Cookie Policy
    </button>
  </div>
</div>

        </div>
      </div>
    </footer>
  );
};

export default Footer;