import React, { useState, useRef, useEffect } from 'react';
import { Award, Users, Target, Zap, CheckCircle, Star, TrendingUp, Globe } from 'lucide-react';
import './About.css';

const About = () => {
  const [inView, setInView] = useState(false);
  const [activeCard, setActiveCard] = useState(null);
  const aboutRef = useRef(null);

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          setInView(true);
        }
      },
      { threshold: 0.2 }
    );

    if (aboutRef.current) {
      observer.observe(aboutRef.current);
    }

    return () => observer.disconnect();
  }, []);

  const values = [
    {
      icon: <Zap />,
      title: 'Innovation First',
      description: 'We push the boundaries of technology to create revolutionary solutions that transform industries.',
      color: '#00FFFF',
      gradient: 'linear-gradient(135deg, #00FFFF, #0066FF)'
    },
    {
      icon: <Users />,
      title: 'Client Partnership',
      description: 'Building long-term relationships through transparency, communication, and exceptional service.',
      color: '#FF00FF',
      gradient: 'linear-gradient(135deg, #FF00FF, #8A2BE2)'
    },
    {
      icon: <Target />,
      title: 'Results Driven',
      description: 'Every project is designed to deliver measurable impact and exceed performance expectations.',
      color: '#FF6600',
      gradient: 'linear-gradient(135deg, #FF6600, #FF0000)'
    },
    {
      icon: <Award />,
      title: 'Excellence Standard',
      description: 'Maintaining the highest quality standards in every aspect of our work and service delivery.',
      color: '#008080',
      gradient: 'linear-gradient(135deg, #008080, #00FFFF)'
    }
  ];

  const achievements = [
    { number: '50+', label: 'Projects Completed', icon: <CheckCircle /> },
    { number: '98%', label: 'Client Satisfaction', icon: <Star /> },
    { number: '10+', label: 'Happy Clients', icon: <Users /> },
    { number: '20+', label: 'Industry Awards', icon: <Award /> },
    { number: '1+', label: 'Years Experience', icon: <TrendingUp /> },
    { number: '1M+', label: 'Global Reach', icon: <Globe /> }
  ];

  const team = [
    {
      name: 'Dr. Samay Chakravarthy',
      role: 'Director',
      image: 'https://images.pexels.com/photos/2379005/pexels-photo-2379005.jpeg',
      description: 'Visionary leader with 15+ years in 3D design and digital innovation.'
    },
    {
      name: 'Venkateshwarlu Pottipalli',
      role: 'Head-Operations',
      image: '/src/assets/images/venkat_sir_image.jpeg',
      description: 'Seasoned operations leader with a strong track record in streamlining processes, enhancing organizational efficiency, and driving cross-functional collaboration to achieve strategic business goals.'
    },
    {
      name: 'Marcus Johnson',
      role: 'Marketing Strategist',
      image: 'https://images.pexels.com/photos/2182970/pexels-photo-2182970.jpeg',
      description: 'Digital marketing guru specializing in growth-driven campaigns.'
    }
  ];

  return (
    <section className={`about ${inView ? 'in-view' : ''}`} ref={aboutRef}>
      <div className="about-background">
        <div className="bg-orb orb-1"></div>
        <div className="bg-orb orb-2"></div>
        <div className="bg-orb orb-3"></div>
      </div>

      <div className="about-container">
        <div className="about-header">
          <div className="header-badge">
            <Star className="badge-icon" />
            <span>About Pixel Horse</span>
          </div>
          <h2 className="about-title">Crafting Digital Excellence</h2>
          <p className="about-subtitle">
            We are pioneers in 3D innovation, transforming ideas into extraordinary 
            digital experiences that captivate and inspire
          </p>
        </div>

        <div className="about-content">
          <div className="story-section">
            <div className="story-text">
              <h3 className="story-title">Our Story</h3>
              <p>
                Founded with the vision to revolutionize digital experiences, Pixel Horse 
                has grown from a small creative studio to a leading force in 3D anamorphic 
                advertising and cutting-edge technology solutions.
              </p>
              <p>
                Our journey began with a simple belief: that technology should inspire, 
                not just function. Today, we combine artistic vision with technical expertise 
                to create solutions that push the boundaries of what's possible.
              </p>
              <div className="story-highlights">
                <div className="highlight">
                  <span className="highlight-number">2025</span>
                  <span className="highlight-text">Company Founded</span>
                </div>
                <div className="highlight">
                  <span className="highlight-number">50+</span>
                  <span className="highlight-text">Projects Delivered</span>
                </div>
                <div className="highlight">
                  <span className="highlight-number">20+</span>
                  <span className="highlight-text">Awards Won</span>
                </div>
              </div>
            </div>
            <div className="story-visual">
              <div className="visual-card main-card">
                <img 
                  src="https://images.pexels.com/photos/3183150/pexels-photo-3183150.jpeg" 
                  alt="Our workspace" 
                />
                <div className="card-overlay">
                  <div className="overlay-content">
                    <h4>Innovation Hub</h4>
                    <p>Where creativity meets technology</p>
                  </div>
                </div>
              </div>
              <div className="visual-card floating-card-1">
                <div className="floating-content">
                  <Zap className="floating-icon" />
                  <span>Cutting-edge Tech</span>
                </div>
              </div>
              <div className="visual-card floating-card-2">
                <div className="floating-content">
                  <Award className="floating-icon" />
                  <span>Award Winning</span>
                </div>
              </div>
            </div>
          </div>

          <div className="values-section">
            <h3 className="section-title">Our Values</h3>
            <div className="values-grid">
              {values.map((value, index) => (
                <div
                  key={index}
                  className={`value-card ${activeCard === index ? 'active' : ''}`}
                  onMouseEnter={() => setActiveCard(index)}
                  onMouseLeave={() => setActiveCard(null)}
                  style={{
                    '--value-color': value.color,
                    '--value-gradient': value.gradient,
                    '--animation-delay': `${index * 0.2}s`
                  }}
                >
                  <div className="value-icon-container">
                    <div className="value-icon">{value.icon}</div>
                    <div className="icon-glow"></div>
                  </div>
                  <h4 className="value-title">{value.title}</h4>
                  <p className="value-description">{value.description}</p>
                  <div className="value-overlay"></div>
                </div>
              ))}
            </div>
          </div>

          <div className="achievements-section">
            <h3 className="section-title">Our Achievements</h3>
            <div className="achievements-grid">
              {achievements.map((achievement, index) => (
                <div
                  key={index}
                  className="achievement-card"
                  style={{ '--achievement-delay': `${index * 0.1}s` }}
                >
                  <div className="achievement-icon">{achievement.icon}</div>
                  <div className="achievement-number">{achievement.number}</div>
                  <div className="achievement-label">{achievement.label}</div>
                  <div className="achievement-glow"></div>
                </div>
              ))}
            </div>
          </div>

          <div className="team-section">
            <h3 className="section-title">Meet Our Team</h3>
            <div className="team-grid">
              {team.map((member, index) => (
                <div
                  key={index}
                  className="team-card"
                  style={{ '--team-delay': `${index * 0.2}s` }}
                >
                  <div className="team-image-container">
                    <img src={member.image} alt={member.name} className="team-image" />
                    <div className="team-overlay">
                      <div className="team-social">
                        <div className="social-icon">
                          <Users />
                        </div>
                      </div>
                    </div>
                  </div>
                  <div className="team-info">
                    <h4 className="team-name">{member.name}</h4>
                    <p className="team-role">{member.role}</p>
                    <p className="team-description">{member.description}</p>
                  </div>
                  <div className="team-glow"></div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default About;