import React, { useState, useEffect } from 'react';
import { ArrowLeft, Play, Download, Star, CheckCircle, Zap, Users, Award, TrendingUp, Eye, Globe, Shield, Wrench } from 'lucide-react';
import ContactForm from '../../../Contact/ContactForm';
import '../../ServiceDetail.css';

const WebsiteManagement = ({ setCurrentSection }) => {
  const [activeTab, setActiveTab] = useState('overview');
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    setIsVisible(true);
  }, []);

  const serviceData = {
    title: 'Website Management',
    subtitle: 'Optimize and Maintain Your Online Presence',
    description: 'Comprehensive website management services to ensure your site is secure, fast, and user-friendly, driving engagement and conversions.',
    hero: 'https://images.pexels.com/photos/392018/pexels-photo-392018.jpeg',
    color: '#32CD32',
    gradient: 'linear-gradient(135deg, #32CD32, #228B22)',
    features: [
      { icon: <Wrench />, title: 'Regular Updates', description: 'Keep your website content and plugins up-to-date' },
      { icon: <Shield />, title: 'Security Monitoring', description: 'Protect your site with advanced security measures' },
      { icon: <Globe />, title: 'Performance Optimization', description: 'Improve load times and user experience' },
      { icon: <TrendingUp />, title: 'SEO Integration', description: 'Ensure your site is optimized for search engines' }
    ],
    portfolio: [
      { title: 'E-Commerce Site Revamp', category: 'Website Management', image: 'https://images.pexels.com/photos/3183150/pexels-photo-3183150.jpeg' },
      { title: 'Corporate Website Maintenance', category: 'Business', image: 'https://images.pexels.com/photos/3184297/pexels-photo-3184297.jpeg' },
      { title: 'Blog Platform Optimization', category: 'Content', image: 'https://images.pexels.com/photos/3184325/pexels-photo-3184325.jpeg' }
    ],
    process: [
      'Website Audit & Assessment',
      'Content & Plugin Updates',
      'Security Enhancements',
      'Performance Optimization',
      'Regular Backups & Monitoring'
    ],
    stats: { websitesManaged: '100+', uptime: '99.9%+', updates: '500+', clients: '50+' }
  };

  const tabs = [
    { id: 'overview', label: 'Overview', icon: <Star /> },
    { id: 'features', label: 'Features', icon: <CheckCircle /> },
    { id: 'portfolio', label: 'Portfolio', icon: <Award /> },
    { id: 'process', label: 'Process', icon: <TrendingUp /> },
    { id: 'contact', label: 'Get Quote', icon: <Users /> }
  ];

  return (
    <div className={`service-detail ${isVisible ? 'visible' : ''}`}>
      <div className="service-hero" style={{ '--service-color': serviceData.color, '--service-gradient': serviceData.gradient }}>
        <div className="hero-background">
          <img src={serviceData.hero} alt={serviceData.title} className="hero-image" />
          <div className="hero-overlay"></div>
        </div>

        <div className="hero-content">
          <button
            className="back-button"
            onClick={() => setCurrentSection('digital-marketing')}
          >
            <ArrowLeft className="back-icon" />
            <span>Back to Digital Marketing</span>
          </button>

          <div className="hero-text">
            <h1 className="hero-title">{serviceData.title}</h1>
            <p className="hero-subtitle">{serviceData.subtitle}</p>
            <p className="hero-description">{serviceData.description}</p>

            <div className="hero-stats">
              {Object.entries(serviceData.stats).map(([key, value]) => (
                <div key={key} className="hero-stat">
                  <div className="stat-value">{value}</div>
                  <div className="stat-label">{key.replace(/([A-Z])/g, ' $1').trim()}</div>
                </div>
              ))}
            </div>

            <div className="hero-actions">
              <button className="action-button primary">
                <span>Get Started</span>
                <Zap className="button-icon" />
              </button>
              <button className="action-button secondary">
                <Play className="button-icon" />
                <span>Watch Demo</span>
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="service-content">
        <div className="content-container">
          <nav className="service-nav">
            {tabs.map(tab => (
              <button
                key={tab.id}
                className={`nav-tab ${activeTab === tab.id ? 'active' : ''}`}
                onClick={() => setActiveTab(tab.id)}
                style={{ '--service-color': serviceData.color, '--service-gradient': serviceData.gradient }}
              >
                {tab.icon}
                <span>{tab.label}</span>
              </button>
            ))}
          </nav>

          <div className="tab-content">
            {activeTab === 'overview' && (
              <div className="overview-content">
                <div className="overview-grid">
                  <div className="overview-text">
                    <h2>Website Management Overview</h2>
                    <p>{serviceData.description}</p>
                    <p>
                      Our website management services ensure your online presence is always at its best, with regular updates, robust security, and performance optimizations to keep your site running smoothly and effectively.
                    </p>
                  </div>
                  <div className="overview-highlights">
                    <h3>Key Benefits</h3>
                    <ul>
                      <li><CheckCircle className="check-icon" /> Improved site reliability</li>
                      <li><CheckCircle className="check-icon" /> Enhanced user experience</li>
                      <li><CheckCircle className="check-icon" /> Stronger security</li>
                      <li><CheckCircle className="check-icon" /> Higher search rankings</li>
                      <li><CheckCircle className="check-icon" /> Reduced downtime</li>
                    </ul>
                  </div>
                </div>
              </div>
            )}

            {activeTab === 'features' && (
              <div className="features-content">
                <h2>Website Management Features</h2>
                <div className="features-grid">
                  {serviceData.features.map((feature, index) => (
                    <div key={index} className="feature-card">
                      <div className="feature-icon">{feature.icon}</div>
                      <h3>{feature.title}</h3>
                      <p>{feature.description}</p>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {activeTab === 'portfolio' && (
              <div className="portfolio-content">
                <h2>Our Website Management Projects</h2>
                <div className="portfolio-grid">
                  {serviceData.portfolio.map((item, index) => (
                    <div key={index} className="portfolio-item">
                      <div className="portfolio-image">
                        <img src={item.image} alt={item.title} />
                        <div className="portfolio-overlay">
                          <Play className="play-icon" />
                        </div>
                      </div>
                      <div className="portfolio-info">
                        <h3>{item.title}</h3>
                        <span className="portfolio-category">{item.category}</span>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {activeTab === 'process' && (
              <div className="process-content">
                <h2>Our Website Management Process</h2>
                <div className="process-timeline">
                  {serviceData.process.map((step, index) => (
                    <div key={index} className="process-step">
                      <div className="step-number">{index + 1}</div>
                      <div className="step-content">
                        <h3>{step}</h3>
                        <div className="step-line"></div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {activeTab === 'contact' && (
              <div className="contact-content">
                <h2>Get Your Website Management Quote</h2>
                <ContactForm serviceType="Website Management" />
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default WebsiteManagement;