import React, { useEffect, useRef, useState } from 'react';
import { ChevronDown, Sparkles, Zap, Globe, ArrowRight, Play, Star } from 'lucide-react';
import { Brain } from 'lucide-react';
import { useNavigate } from 'react-router-dom';
import './Hero.css';
import Grid1Background from 'https://cdn.jsdelivr.net/npm/threejs-components@0.0.16/build/backgrounds/grid1.cdn.min.js';

const Hero = () => {
  const heroRef = useRef(null);
  const canvasRef = useRef(null);
  const gridRef = useRef(null);
  const coinContainerRef = useRef(null);
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 });
  const [scrollProgress, setScrollProgress] = useState(0);
  const navigate = useNavigate();
  const sceneRef = useRef(null);
  const rendererRef = useRef(null);
  const cameraRef = useRef(null);
  const coinRef = useRef(null);
  const animationIdRef = useRef(null);

  // Initialize the hexagonal grid background
  useEffect(() => {
    if (canvasRef.current && !gridRef.current) {
      gridRef.current = Grid1Background(canvasRef.current);
    }
    return () => {
      // Cleanup if necessary
      if (gridRef.current) {
        // Assuming Grid1Background has a dispose method; adjust based on library documentation
        gridRef.current = null;
      }
    };
  }, []);

  // Initialize Three.js scene for the coin
  useEffect(() => {
    if (!coinContainerRef.current) return;

    const initCoinScene = (THREE) => {
      // Create a fallback coin geometry
      const createFallbackCoin = () => {
        const geometry = new THREE.CylinderGeometry(1, 1, 0.2, 32);
        const material = new THREE.MeshPhongMaterial({ 
          color: 0xffd700,
          shininess: 100,
          emissive: 0x444400
        });
        const coin = new THREE.Mesh(geometry, material);
        coinRef.current = coin;
        scene.add(coin);
        animateCoin();
      };

      // Set up scene
      const scene = new THREE.Scene();
      sceneRef.current = scene;

      // Set up camera
      const camera = new THREE.PerspectiveCamera(
        45,
        coinContainerRef.current.offsetWidth / coinContainerRef.current.offsetHeight,
        0.1,
        1000
      );
      camera.position.z = 5;
      cameraRef.current = camera;

      // Set up renderer
      const renderer = new THREE.WebGLRenderer({ alpha: true, antialias: true });
      renderer.setSize(
        coinContainerRef.current.offsetWidth,
        coinContainerRef.current.offsetHeight
      );
      renderer.setClearColor(0x000000, 0);
      coinContainerRef.current.appendChild(renderer.domElement);
      rendererRef.current = renderer;

      // Add lights
      const ambientLight = new THREE.AmbientLight(0xffffff, 0.5);
      scene.add(ambientLight);

      const directionalLight = new THREE.DirectionalLight(0xffffff, 1);
      directionalLight.position.set(1, 1, 1);
      scene.add(directionalLight);

      const pointLight = new THREE.PointLight(0x00ffff, 1, 100);
      pointLight.position.set(2, 2, 2);
      scene.add(pointLight);

      // Animation function
      const animateCoin = () => {
        if (coinRef.current) {
          coinRef.current.rotation.y += 0.01;
          coinRef.current.rotation.x += 0.005;
        }
        
        renderer.render(scene, camera);
        animationIdRef.current = requestAnimationFrame(animateCoin);
      };

      // Load the coin model using FBXLoader
      // Use the same THREE instance for FBXLoader
      const FBXLoader = THREE.FBXLoader || (THREE.default && THREE.default.FBXLoader);
      
      if (FBXLoader) {
        const loader = new FBXLoader();
        loader.load(
          '/src/public/images/Coin.fbx', // Update this path to your actual coin model location
          (fbx) => {
            const coin = fbx;
            coinRef.current = coin;
            scene.add(coin);
            
            // Scale and position the coin
            coin.scale.set(0.5, 0.5, 0.5);
            coin.position.y = 0;
            
            // Start animation
            animateCoin();
          },
          undefined,
          (error) => {
            console.error('Error loading coin model:', error);
            // Fallback to a basic geometry if model fails to load
            createFallbackCoin();
          }
        );
      } else {
        // Fallback if FBXLoader is not available
        createFallbackCoin();
      }

      // Handle window resize
      const handleResize = () => {
        if (!coinContainerRef.current) return;
        camera.aspect = coinContainerRef.current.offsetWidth / coinContainerRef.current.offsetHeight;
        camera.updateProjectionMatrix();
        renderer.setSize(
          coinContainerRef.current.offsetWidth,
          coinContainerRef.current.offsetHeight
        );
      };

      window.addEventListener('resize', handleResize);

      // Cleanup function
      return () => {
        window.removeEventListener('resize', handleResize);
        if (animationIdRef.current) {
          cancelAnimationFrame(animationIdRef.current);
        }
        if (renderer) {
          renderer.dispose();
        }
      };
    };

    // Check if Three.js is already available from the Grid1Background import
    if (window.THREE) {
      initCoinScene(window.THREE);
    } else {
      // If not, dynamically import Three.js
      import('three').then(THREE => {
        // Also import FBXLoader
        import('three/examples/jsm/loaders/FBXLoader.js').then(module => {
          // Add FBXLoader to THREE
          window.THREE = window.THREE || THREE;
          window.THREE.FBXLoader = module.FBXLoader;
          initCoinScene(window.THREE);
        }).catch(error => {
          console.error('Failed to load FBXLoader:', error);
          initCoinScene(THREE);
        });
      });
    }
  }, []);

  // Handle random colors button click
  const handleRandomColors = () => {
    if (gridRef.current) {
      gridRef.current.grid.setColors([
        0xffffff * Math.random(),
        0xffffff * Math.random(),
        0xffffff * Math.random(),
      ]);
      gridRef.current.grid.light1.color.set(0xffffff * Math.random());
      gridRef.current.grid.light1.intensity = 500 + Math.random() * 1000;
      gridRef.current.grid.light2.color.set(0xffffff * Math.random());
      gridRef.current.grid.light2.intensity = 250 + Math.random() * 250;
    }
  };

  // Handle mouse movement for interactive effects
  useEffect(() => {
    const handleMouseMove = (e) => {
      setMousePosition({
        x: (e.clientX / window.innerWidth) * 2 - 1,
        y: (e.clientY / window.innerHeight) * 2 - 1,
      });
    };

    window.addEventListener('mousemove', handleMouseMove);
    return () => window.removeEventListener('mousemove', handleMouseMove);
  }, []);

  // Handle scroll with requestAnimationFrame for smooth performance
  useEffect(() => {
    let animationFrameId;
    const handleScroll = () => {
      if (animationFrameId) cancelAnimationFrame(animationFrameId);
      animationFrameId = requestAnimationFrame(() => {
        if (!heroRef.current) return;
        const scrollTop = window.scrollY;
        const windowHeight = window.innerHeight;
        const maxScroll = windowHeight * 0.4;
        const progress = Math.min(scrollTop / maxScroll, 1);
        setScrollProgress(progress);
      });
    };

    window.addEventListener('scroll', handleScroll, { passive: true });
    return () => {
      window.removeEventListener('scroll', handleScroll);
      if (animationFrameId) cancelAnimationFrame(animationFrameId);
    };
  }, []);

  // Handle card click navigation
  const handleCardClick = (path) => {
    navigate(path);
  };

  return (
    <section
      className="ph2-hero"
      ref={heroRef}
      style={{
        height: `calc(100vh - ${scrollProgress * 50}vh)`,
        opacity: 1 - scrollProgress * 0.25,
        borderBottomLeftRadius: `${scrollProgress * 100}px`,
        borderBottomRightRadius: `${scrollProgress * 100}px`,
        transform: `scale(${1 - scrollProgress * 0.2})`,
      }}
    >
      <canvas
        id="webgl-canvas"
        ref={canvasRef}
        style={{
          position: 'fixed',
          top: 0,
          right: 0,
          bottom: 0,
          left: 0,
          zIndex: 0,
          width: '100%',
          height: '100%',
        }}
      ></canvas>

      <div className="ph2-hero-content">
        <div className="ph2-hero-text">
          <div className="ph2-hero-badge">
            <Star className="ph2-badge-icon" />
            <span>Premium 3D Innovation</span>
          </div>

          <h1 className="ph2-hero-title">
            <span className="ph2-title-line ph2-animate-slide-in">Welcome to the Future</span>
            <img
             src="public/images/Logo.png"
              alt="Pixel Horse Logo"
              className="ph2-title-main ph2-animate-scale-in ph-logo-image"
            />
            <span className="ph2-title-subtitle ph2-animate-slide-in-right">3D Anamorphic Experiences</span>
          </h1>

          <p className="ph2-hero-description ph2-animate-fade-in">
            Transform your digital presence with cutting-edge 3D anamorphic advertising, revolutionary AI systems, and
            innovative solutions that push the boundaries of technology and creativity.
          </p>

          <div className="ph2-hero-stats ph2-animate-fade-in">
            <div className="ph2-stat-item">
              <div className="ph2-stat-number">50+</div>
              <div className="ph2-hero-stat-label">Projects Delivered</div>
            </div>
            <div className="ph2-stat-item">
              <div className="ph2-stat-number">98%</div>
              <div className="ph2-hero-stat-label">Client Satisfaction</div>
            </div>
            <div className="ph2-stat-item">
              <div className="ph2-stat-number">24/7</div>
              <div className="ph2-hero-stat-label">Support Available</div>
            </div>
          </div>

          <div className="ph2-hero-buttons ph2-animate-fade-in">
            <button className="ph2-cta-button ph2-cta-button-primary">
              <Zap className="ph2-button-icon" />
              <span>Explore Our Services</span>
              <ArrowRight className="ph2-button-arrow" />
            </button>
            <button className="ph2-cta-button ph2-cta-button-secondary">
              <Play className="ph2-button-icon" />
              <span>Watch Demo Reel</span>
            </button>
          </div>
        </div>

        <div className="ph2-hero-visual">
          <div className="ph2-floating-elements">
            <div
              className="ph2-floating-card ph2-card-1 ph2-animate-float"
              onClick={() => handleCardClick('/digital-marketing')}
            >
              <div className="ph2-card-inner">
                <Sparkles className="ph2-card-icon" />
                <span className="ph2-card-text-Advertising">Digital Marketing</span>
                <div className="ph2-card-glow"></div>
              </div>
            </div>
            <div
              className="ph2-floating-card ph2-card-2 ph2-animate-float"
              onClick={() => handleCardClick('/uiux-design')}
            >
              <div className="ph2-card-inner">
                <Brain className="ph2-card-icon" />
                <span className="ph2-card-text-Systems">UX/UI Design</span>
                <div className="ph2-card-glow"></div>
              </div>
            </div>
            <div
              className="ph2-floating-card ph2-card-3 ph2-animate-float"
              onClick={() => handleCardClick('/3d-advertising')}
            >
              <div className="ph2-card-inner">
                <Globe className="ph2-card-icon" />
                <span className="ph2-card-text-Solutions">3D Advertising</span>
                <div className="ph2-card-glow"></div>
              </div>
            </div>
          </div>

          <div
            className="ph2-central-display"
            style={{
              transform: `scale(${1 - scrollProgress * 0.3})`,
              opacity: 1 - scrollProgress * 0.5,
            }}
          >
            <div 
              className="ph2-coin-container"
              ref={coinContainerRef}
              style={{
                width: '100%',
                height: '100%',
                position: 'relative'
              }}
            ></div>
          </div>
        </div>
      </div>

      <div className="buttons">
        <button type="button" id="colors-btn" onClick={handleRandomColors}>
          
        </button>
      </div>

      <div
        className="ph2-scroll-indicator ph2-animate-bounce"
        style={{
          opacity: 1 - scrollProgress * 1.5,
        }}
      >
        <div className="ph2-scroll-line"></div>
        <ChevronDown className="ph2-scroll-icon" />
        <span className="ph2-scroll-text">Discover More</span>
      </div>
    </section>
  );
};

export default Hero;