import React, { useState, useEffect } from 'react';
import { ArrowLeft, Play, Download, Star, CheckCircle, Zap, Users, Award, TrendingUp, Eye, Search, Globe, BarChart } from 'lucide-react';
import ContactForm from '../../../Contact/ContactForm';
import '../../ServiceDetail.css';

const SEOServices = ({ setCurrentSection }) => {
  const [activeTab, setActiveTab] = useState('overview');
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    setIsVisible(true);
  }, []);

  const serviceData = {
    title: 'SEO Services',
    subtitle: 'Boost Your Online Visibility',
    description: 'Comprehensive SEO strategies to improve your websites ranking on search engines, drive organic traffic, and enhance brand authority.',
    hero: 'https://images.pexels.com/photos/669615/pexels-photo-669615.jpeg',
    color: '#FF6347',
    gradient: 'linear-gradient(135deg, #FF6347, #FF4500)',
    features: [
      { icon: <Search />, title: 'Keyword Optimization', description: 'Targeted keyword research to drive relevant traffic' },
      { icon: <Globe />, title: 'On-Page SEO', description: 'Optimize content, meta tags, and site structure for search engines' },
      { icon: <BarChart />, title: 'Link Building', description: 'High-quality backlinks to boost domain authority' },
      { icon: <TrendingUp />, title: 'Analytics & Reporting', description: 'Detailed insights to track performance and ROI' }
    ],
    portfolio: [
      { title: 'E-Commerce SEO Campaign', category: 'Retail', image: 'https://images.pexels.com/photos/3183150/pexels-photo-3183150.jpeg' },
      { title: 'Local Business SEO', category: 'Local SEO', image: 'https://images.pexels.com/photos/3184297/pexels-photo-3184297.jpeg' },
      { title: 'B2B Lead Generation', category: 'Enterprise SEO', image: 'https://images.pexels.com/photos/3184325/pexels-photo-3184325.jpeg' }
    ],
    process: [
      'Website Audit & Analysis',
      'Keyword Research & Strategy',
      'On-Page & Technical SEO',
      'Content Creation & Optimization',
      'Link Building & Outreach',
      'Performance Tracking & Reporting'
    ],
    stats: { clients: '150+', keywordsRanked: '10K+', trafficIncrease: '200%+', industries: '30+' }
  };

  const tabs = [
    { id: 'overview', label: 'Overview', icon: <Star /> },
    { id: 'features', label: 'Features', icon: <CheckCircle /> },
    { id: 'portfolio', label: 'Portfolio', icon: <Award /> },
    { id: 'process', label: 'Process', icon: <TrendingUp /> },
    { id: 'contact', label: 'Get Quote', icon: <Users /> }
  ];

  return (
    <div className={`service-detail ${isVisible ? 'visible' : ''}`}>
      <div className="service-hero" style={{ '--service-color': serviceData.color, '--service-gradient': serviceData.gradient }}>
        <div className="hero-background">
          <img src={serviceData.hero} alt={serviceData.title} className="hero-image" />
          <div className="hero-overlay"></div>
        </div>

        <div className="hero-content">
          <button
            className="back-button"
            onClick={() => setCurrentSection('digital-marketing')}
          >
            <ArrowLeft className="back-icon" />
            <span>Back to Digital Marketing</span>
          </button>

          <div className="hero-text">
            <h1 className="hero-title">{serviceData.title}</h1>
            <p className="hero-subtitle">{serviceData.subtitle}</p>
            <p className="hero-description">{serviceData.description}</p>

            <div className="hero-stats">
              {Object.entries(serviceData.stats).map(([key, value]) => (
                <div key={key} className="hero-stat">
                  <div className="stat-value">{value}</div>
                  <div className="stat-label">{key.replace(/([A-Z])/g, ' $1').trim()}</div>
                </div>
              ))}
            </div>

            <div className="hero-actions">
              <button className="action-button primary">
                <span>Get Started</span>
                <Zap className="button-icon" />
              </button>
              <button className="action-button secondary">
                <Play className="button-icon" />
                <span>Watch Demo</span>
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="service-content">
        <div className="content-container">
          <nav className="service-nav">
            {tabs.map(tab => (
              <button
                key={tab.id}
                className={`nav-tab ${activeTab === tab.id ? 'active' : ''}`}
                onClick={() => setActiveTab(tab.id)}
                style={{ '--service-color': serviceData.color, '--service-gradient': serviceData.gradient }}
              >
                {tab.icon}
                <span>{tab.label}</span>
              </button>
            ))}
          </nav>

          <div className="tab-content">
            {activeTab === 'overview' && (
              <div className="overview-content">
                <div className="overview-grid">
                  <div className="overview-text">
                    <h2>SEO Services Overview</h2>
                    <p>{serviceData.description}</p>
                    <p>
                      Our SEO services combine technical expertise, content strategy, and data-driven insights to improve your websites visibility, attract high-quality traffic, and convert visitors into loyal customers.
                    </p>
                  </div>
                  <div className="overview-highlights">
                    <h3>Key Benefits</h3>
                    <ul>
                      <li><CheckCircle className="check-icon" /> Higher search engine rankings</li>
                      <li><CheckCircle className="check-icon" /> Increased organic traffic</li>
                      <li><CheckCircle className="check-icon" /> Enhanced brand credibility</li>
                      <li><CheckCircle className="check-icon" /> Measurable ROI</li>
                      <li><CheckCircle className="check-icon" /> Long-term growth</li>
                    </ul>
                  </div>
                </div>
              </div>
            )}

            {activeTab === 'features' && (
              <div className="features-content">
                <h2>SEO Service Features</h2>
                <div className="features-grid">
                  {serviceData.features.map((feature, index) => (
                    <div key={index} className="feature-card">
                      <div className="feature-icon">{feature.icon}</div>
                      <h3>{feature.title}</h3>
                      <p>{feature.description}</p>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {activeTab === 'portfolio' && (
              <div className="portfolio-content">
                <h2>Our SEO Projects</h2>
                <div className="portfolio-grid">
                  {serviceData.portfolio.map((item, index) => (
                    <div key={index} className="portfolio-item">
                      <div className="portfolio-image">
                        <img src={item.image} alt={item.title} />
                        <div className="portfolio-overlay">
                          <Play className="play-icon" />
                        </div>
                      </div>
                      <div className="portfolio-info">
                        <h3>{item.title}</h3>
                        <span className="portfolio-category">{item.category}</span>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {activeTab === 'process' && (
              <div className="process-content">
                <h2>Our SEO Process</h2>
                <div className="process-timeline">
                  {serviceData.process.map((step, index) => (
                    <div key={index} className="process-step">
                      <div className="step-number">{index + 1}</div>
                      <div className="step-content">
                        <h3>{step}</h3>
                        <div className="step-line"></div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {activeTab === 'contact' && (
              <div className="contact-content">
                <h2>Get Your SEO Quote</h2>
                <ContactForm serviceType="SEO Services" />
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default SEOServices;